import { withPrefix } from 'gatsby';
import React from 'react';
import { withIntl } from '../i18n';
import PageWrapper from '../components/PageWrapper';
import Paragraphs from '../components/Paragraphs';
import { ContentTitle, MultiBreaker, PageTitle, Wysiwyg } from '../ui/utilities';

const Frames = ({ t, ...context }) => {
  const slot1 = (
    <MultiBreaker>
      <img src={withPrefix('assets/img/frames-wall.jpg')} alt='' />
      <img src={withPrefix('assets/img/frames-pile.jpg')} alt='' />
    </MultiBreaker>
  );
  const slot2 = (
    <MultiBreaker>
      <img src={withPrefix('assets/img/frames-process.jpg')} alt='' />
      <img src={withPrefix('assets/img/frames-show.jpg')} alt='' />
    </MultiBreaker>
  );

  return (
    <PageWrapper
      title={t('frames.title')}
      description={t('frames.metas.description')}
      reader
      {...context}>
      <PageTitle>{t('frames.title')}</PageTitle>
      <ContentTitle className='t-h1' aria-hidden='true'>
        {t('frames.title')}
      </ContentTitle>
      <Wysiwyg>
        <Paragraphs lines={t('frames.description')} margin components={[ slot1, slot2 ]} />
      </Wysiwyg>
    </PageWrapper>
  );
};

export default withIntl()(Frames);
